
























export default {
    data() {
        return {
            title: '插屏弹窗',
            bannerShow: true
        }
    },
    onBackPress() {
    	if (this.bannerShow) {
    		this.bannerShow = false;
    		return true;
    	}
    },
    methods: {
        closeBanner: function() {
            this.bannerShow = false;
        },
        showBanner: function() {
            this.bannerShow = true;
        },
    }
}
